import React, { useState, useEffect } from "react";
import { Input, Typography, Row, Col, Table, Pagination } from "antd";
import { get_leaderboard, post_user_leaderboard } from "../request/api";

const { Text } = Typography


function Leaderboard() {
  const [players, setPlayers] = useState([]);
  const [username, setUsername] = useState(localStorage.getItem("username"));
  const [scores, setScores] = useState([]);

  const score = localStorage.getItem("score")

  const handleUsernameChange = (event) => {
    event.preventDefault();

    console.log("name:", event.target.value)
    setUsername(event.target.value)
    localStorage.setItem("username", event.target.value)

    // post_user_leaderboard({
    //   uuid: uuid,
    //   username: username,
    // })
    //   .then((response) => {})
    //   .catche((error) => {
    //     console.log("post user leaderboard:", error);
    //   });
  }

  const uuid = localStorage.getItem("uuid")

  // 对玩家按分数降序排序
  // players.sort((a, b) => b.score - a.score);

  useEffect(() => {
    get_leaderboard()
      .then((data) => {
        console.log("get leaderboard success", data);
        setScores(data);
      })
      .catch((error) => {
        console.log("get_leaderboard:", error);
      });
  }, []);

  const columns = [
    // {
    //   title: "ID",
    //   dataIndex: "id",
    //   key: "id",
    // },
    {
      title: "用户",
      dataIndex: "username",
      key: "id",
    },
    {
      title: "分数",
      dataIndex: "score",
      key: "id",
    },
  ];

  const highlightRowClassName = (record) => {
    return record.id === uuid ? 'highlight-row' : '';
  };

  console.log("scores:", scores, uuid)

  return (
    <div>
      {/* {score && score > 0 &&
        <Row>
          <Col span={16}>
            <Input placeholder="输入您的姓名" value={username} onChange={handleUsernameChange} />
          </Col>
          <Col span = {6} offset={2}>
            <Text> 分数: {score} </Text>
          </Col>
        </Row>
      } */}

      <Table
        dataSource={scores}
        columns={columns}
        rowKey={(record) => record.id} // Add unique key prop here
        rowClassName={highlightRowClassName}
      ></Table>
      {/* <Pagination
        defaultCurrent={1}
        total={500}
        responsive={true} /> */}
    </div>
  );
}

export default Leaderboard;
